body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.music-player-paper {
  width: 300px;
  height: auto;
  margin: 60px auto;
  padding: 10px;
  overflow: hidden;
}
.album-title {
  animation: title 13s linear infinite;
  width: 100%;
  animation-delay: 1.9s;
}
@keyframes title {
  50% {
    opacity: 1;
    margin-left: -300px;
  }
  50.09% {
    opacity: 0;
  }
  50.21% {
    opacity: 0;
    margin-left: 101%;
  }
  51% {
    opacity: 1;
  }
}
.album-image {
  width: 60%;
  margin: 10px auto;
  height: 180px;
}
.album-image img {
  object-fit: cover;
  border-radius: 50%;
}
.album-image .skeleton {
  height: 180px;
  width: 180px;
}
.music-slider {
  width: 90%;
  margin: 0px auto;
}
.album-artist {
  padding: 0px 10px;
  opacity: 0.9;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.bottom-nav {
  margin-top: 20px;
}
.volume-slider-paper {
  height: 50px;
  width: 100%;
}
.volume-slider {
  margin: 10px auto;
  width: 60%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
